import React, { Component } from 'react';
import * as config from '../../constants/config';
import moment from 'moment'
import Button from '@material-ui/core/Button';
import Modal from 'react-responsive-modal';
import callApi from '../../utils/apiCaller';
import Swal from 'sweetalert2'
class InvoiceDetails extends Component {

    constructor (props) {
        super(props)
        this.state = {
            note: this.props.order.note ? this.props.order.note : ' ',
            discount: this.props.order.discount ? this.props.order.discount : 0,
            open: false,
            openDiscount: false
        }
    }
    onCloseModal = () => {
		this.setState({ open: false });
    };

    openModal = () => {
        this.setState({ open: true});
    }
    onCloseModalDiscount = () => {
		this.setState({ openDiscount: false });
    };

    openDiscount = () => {
        this.setState({ openDiscount: true});
    }
    onChangeForm = (event) => {
		var target = event.target;
		var name = target.name;
		var value = target.type === 'checkbox'? target.checked:target.value;
		this.setState({
			[name]: value
		});
    }
    
    onhandleNoteCustomer = (id) => {
        var {note} = this.state;
        var data = {note: note};
        callApi('PUT', config.ORDER_URL  + "/" + id, data).then( res => {
            if(res && res.data.status){
                this.setState({ open: false });
                Swal('Sa din gii thnh cng','','success')
            }
        });
        
    }

    onhandleSetDiscount = (id) => {
        var {discount} = this.state
        var data = {discount: discount}
        callApi('PUT', config.ORDER_URL  + "/" + id, data).then( res => {
            if(res && res.data.status){
                this.setState({ openDiscount: false });
                Swal('Sa gim gi thnh cng','','success')
            }
        });
        // console.log(data);
    }

    format_money = (amount, decimalCount = 0, decimal = ".", thousands = ",") => {
        try {
          decimalCount = Math.abs(decimalCount);
          decimalCount = isNaN(decimalCount) ? 2 : decimalCount;
      
          const negativeSign = amount < 0 ? "-" : "";
      
          let i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString();
          let j = (i.length > 3) ? i.length % 3 : 0;
      
          return negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : "");
        } catch (e) {
          console.log(e)
        }
    };

    todayNow = () => {
        var today = new Date();
        var dd = today.getDate();
        var mm = today.getMonth() + 1; //January is 0!
        var yyyy = today.getFullYear();
        if (dd < 10) {
            dd = '0' + dd;
        } 
        if (mm < 10) {
            mm = '0' + mm;
        } 
        var now = dd + ' thng ' + mm + ' nm ' + yyyy;
        return now;
    }
    
    render() {
        var {order} = this.props;
        var name = order.customer ? order.customer.firstname + ' ' + order.customer.lastname : ''
        return (
            
                <div className="card-body">
                    <div className="row mb-4">
                    <h3 className="text-center margin-top20">  <strong> Phiu thu </strong> <br/></h3>
                    <div className="card-header text-center">
                        Ngy: {this.todayNow()}
                        <p><strong>Lin 2 giao cho khch hng </strong> </p>
                    </div>
                    <br/>
                        <div className="col-sm-6">
                            
                            <div>
                                Tn khch hng: <strong>{name} </strong>
                            </div>
                            <div>in thoi: {order.customer ? order.customer.phone : ''}</div>
                            <div>a ch: {order.customer ? order.customer.address : ''}</div>
                            <div>Din gii: {this.state.note}    
                                <Button type="submit" className="btn btn-primary btn-cons-small" style={{marginLeft: 20}} variant="fab" color="secondary" size="small"  onClick={ () => this.openModal(order.id)}>
                                    <i className="material-icons">create</i>
                                </Button></div>
                            
                        </div>
                        <div className="col-sm-6">
                            <div>M n: <strong>{order.transaction_id}</strong></div>
                            <div>Thng tin:  {order.customer && order.customer.gender === config.GENDER_MALE ? 'N' : 'Nam'}  {order.customer && order.customer.birthday ? "- " + moment(order.customer.birthday).format('DD/MM/YYYY ') : ''}</div>
                        </div>
                    </div>
                  
                    <div className="table-responsive-sm margin-top20">
                        <table className="table table-bordered">

                        <thead>
                            <tr>
                                <th className="text-center">STT</th>
                                <th className="text-center">Dch v</th>
                                <th className="text-center">S lng</th>
                                <th className="text-center">n gi</th>
                                <th className="text-center">Thnh tin</th>
                               
                            </tr>
                        </thead>
                            <tbody>

                                {this.showData(order.order_detail)}
                                <tr>
                                    <td colSpan="4" className="text-left">
                                    Cng
                                    </td>
                                    <td className="text-right"><div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">{this.format_money(order.total )}</div></td>
                                </tr>
                                <tr>
                                    <td colSpan="5" className="text-left">
                                        <div className="col-md-4 col-sm-4 col-xs-4"> T l CK </div>
                                        <div className="col-md-4 col-sm-4 col-xs-4"> 100% s tin chit khu 
                                            <Button type="submit" className="btn btn-primary btn-cons-small" style={{marginLeft: 20}} variant="fab" color="secondary" size="small"  onClick={ () => this.openDiscount(order.id)}>
                                                <i className="material-icons">create</i>
                                            </Button>
                                        </div>
                                        <div className="col-md-4 col-sm-4 col-xs-4 text-right"> {this.format_money(order.total*(this.state.discount/100))} </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td colSpan="5" className="text-left">
                                        <div className="col-md-4 col-sm-4 col-xs-4">  </div>
                                        <div className="col-md-4 col-sm-4 col-xs-4"> Cng tin hng ( tr CK) </div>
                                        <div className="col-md-4 col-sm-4 col-xs-4 text-right"> {this.format_money(order.total - (order.total*(this.state.discount/100)))} </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td colSpan="5" className="text-left">
                                        <div className="col-md-4 col-sm-4 col-xs-4">  </div>
                                        <div className="col-md-4 col-sm-4 col-xs-4"> Tng tin thanh ton </div>
                                        <div className="col-md-4 col-sm-4 col-xs-4 text-right"> {this.format_money(order.total - (order.total*(this.state.discount/100)))} </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div><p>S tin vit bng ch: ..................................................................................................................................................................................................................</p></div>
                   <br/>
                    <div className="row">
                        
                        <div className="col-lg-4 col-sm-4 col-xs-4 col-md-4">
                            <b>NGI NP TIN</b>
                        </div>
                        <div className="col-lg-4 col-sm-4 col-xs-4 col-md-4">
                            <b>NGI KIM SOT</b>
                        </div>
                        <div className="col-lg-4 col-sm-4 col-xs-4 col-md-4 text-center">
                            <i>Ngy ..... thng ..... nm .....</i>
                            <br/>
                            <b>NGI THU TIN</b>
                        </div>
                    </div>
                    <Modal open={this.state.open} onClose={this.onCloseModal} center>
                        <div className="form-group">
                            <label>Din gii</label>
                            <input 
                                type="text" 
                                className="form-control" 
                                value={this.state.note}
                                onChange={this.onChangeForm} 
                                name="note" 
                                placeholder="Din gii"/>
                        </div>
                        <p>
                            <Button 
                                className="btn btn-primary float-right" 
                                variant="contained" 
                                color="primary" 
                                onClick={ () => { this.onhandleNoteCustomer(order.id) } }
                                >Lu</Button>
                        </p>
                    </Modal>

                    <Modal open={this.state.openDiscount} onClose={this.onCloseModalDiscount} center>
                        <div className="form-group">
                            <label>Gim gi</label>
                            <input 
                                type="number" 
                                className="form-control" 
                                value={this.state.discount}
                                onChange={this.onChangeForm} 
                                name="discount" 
                                placeholder="Gim gi"/>
                        </div>
                        <p>
                            <Button 
                                className="btn btn-primary float-right" 
                                variant="contained" 
                                color="primary" 
                                onClick={ () => { this.onhandleSetDiscount(order.id) } }
                                >Lu</Button>
                        </p>
                    </Modal>
                </div>
         
        );
    }

    showData(order_detail){
        var result = null;
		if(order_detail && typeof order_detail !== 'undefined' && order_detail.length > 0){
			result = order_detail.map((item, index) => {
				return  (<tr key={index}>
                    <td className="text-center">{index+1}</td>
                    <td className="text-left">{item.product_title}</td>
                    <td className="text-center">{item.quantity}</td>
                    <td className="text-center">{this.format_money(item.product_price)}</td>
                    <td className="text-right">
                        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">{this.format_money(item.totalPriceQuantity)}</div>
                    </td>
                </tr>);
			})
			
		}
		return result;
       
    }
}

export default InvoiceDetails;